.modal {
  opacity: 0;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  transition: opacity var(--hoverTime) ease-in-out;

  &.is-shown {
    opacity: 1;
    pointer-events: all;
  }

  &__milk {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(73, 73, 73, 0.3);
    z-index: 1;
  }

  &__box {
    position: relative;
    background-color: var(--tooltipBg);
    z-index: 2;
    min-width: 40rem;
    border-radius: 3px;
    padding: 3rem 4rem;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
  }
}
